import PlateType from '../../../js/enum.js'
import { customItem, getYMDFormateTime } from '../../../js/utility.js'

export function getReceivablesAddInfo(name, userInfo) { //data 
  var tempList = [];
  tempList.push(customItem('', 'contract_id', "contract", "1", '', '', ''));
  tempList.push(customItem('', 'customer_id', "customer", "1", '', '', '', true));
  tempList.push(customItem('', 'name', "text", "1", name, name));
  tempList.push(customItem('', 'price', "text", "1"));
  tempList.push(customItem('', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('', 'description', "textarea", "1"));
  return tempList;
}

export function getReceivablesEditInfo(data) { //data 
  var tempList = [];
  tempList.push(customItem('', 'contract_id', "contract", "1", data.contract.contract_id, data.contract.number, ''));
  tempList.push(customItem('', 'customer_id', "customer", "1", data.customer_id, data.customer_name, '', true));
  tempList.push(customItem('', 'name', "text", "1", data.name, data.name));
  tempList.push(customItem('', 'price', "text", "1", data.price, data.price));
  tempList.push(customItem('', 'pay_time', "datetime", "1", getYMDFormateTime(data.pay_time), getYMDFormateTime(data.pay_time), 'YYYY-MM-DD'));
  tempList.push(customItem('', 'owner_role_id', "user", "1", data.owner_role_id, data.owner_name));
  tempList.push(customItem('', 'description', "textarea", "1", data.description, data.description));
  return tempList;
}

export function getReceivablesShowInfo(data) { //data 
  var tempList = [];
  tempList.push({ name: '', val: data.name, type: 0, id: "" });
  tempList.push({ name: '', val: data.contract.number, type: parseInt(PlateType.Contract), id: data.contract.contract_id });
  tempList.push({ name: '', val: data.customer_name, type: parseInt(PlateType.Customer), id: data.customer_id });
  tempList.push({ name: '', val: data.price, type: 0, id: "" });
  tempList.push({ name: '', val: getYMDFormateTime(data.pay_time), type: 0, id: "" });
  tempList.push({ name: '', val: data.owner_name, type: parseInt(PlateType.AddressBook), id: data.owner_role_id });
  tempList.push({ name: '', val: data.creator_name, type: parseInt(PlateType.AddressBook), id: data.creator_role_id });
  tempList.push({ name: '', val: getYMDFormateTime(data.create_time), type: 0, id: "" });
  tempList.push({ name: '', val: data.description, type: 0, id: "" });
  return tempList;
}

export function getReceivingorderAddInfo(data, userInfo) { //data    
  var tempList = [];
  tempList.push(customItem('', 'receivables_id', "receivables", "1", '', ''));
  tempList.push(customItem('', 'account_id', "select", "1", '', '', data));
  tempList.push(customItem('/', 'receipt_account', "text", "1", '', '', '', true));
  tempList.push(customItem('', 'company', "text", "1", '', '', '', true));
  tempList.push(customItem('', 'money', "text", "1"));
  tempList.push(customItem('', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('', 'description', "textarea", "1"));
  return tempList;
}

export function getReceivingorderEditInfo(list, data) { //data    
  var tempList = [];
  tempList.push(customItem('', 'receivables_id', "receivables", "1", data.receivables_id, data.receivables_name));
  tempList.push(customItem('', 'account_id', "select", "1", data.bank_account_id, '', list));
  tempList.push(customItem('/', 'receipt_account', "text", "1", data.receipt_account, data.receipt_account, '', true));
  tempList.push(customItem('', 'company', "text", "1", data.company, data.company, '', true));
  tempList.push(customItem('', 'money', "text", "1", data.money, data.money));
  tempList.push(customItem('', 'owner_role_id', "user", "1", data.owner_role_id, data.owner_name));
  tempList.push(customItem('', 'pay_time', "datetime", "1", getYMDFormateTime(data.pay_time), getYMDFormateTime(data.pay_time), 'YYYY-MM-DD'));
  tempList.push(customItem('', 'description', "textarea", "1", data.description, data.description));
  return tempList;
}

export function getReceivingorderShowInfo(data) { //data 
  var tempList = [];
  tempList.push({ name: '', val: data.name, type: 0, id: "" });
  tempList.push({ name: '', val: data.receivables_name, type: parseInt(PlateType.FinanceReceivables), id: data.receivables_id });
  tempList.push({ name: '', val: getYMDFormateTime(data.pay_time), type: 0, id: "" });
  tempList.push({ name: '', val: data.money, type: 0, id: "" });
  tempList.push({ name: '', val: data.owner_name, type: parseInt(PlateType.AddressBook), id: data.owner_role_id });
  tempList.push({ name: '', val: data.creator_name, type: parseInt(PlateType.AddressBook), id: data.creator_role_id });
  tempList.push({ name: '', val: data.receipt_bank, type: 0, id: "" });
  tempList.push({ name: '/', val: data.receipt_account, type: 0, id: '' });
  tempList.push({ name: '', val: data.company, type: 0, id: "" });
  tempList.push({ name: '', val: getYMDFormateTime(data.create_time), type: 0, id: "" });
  tempList.push({ name: '', val: data.description, type: 0, id: "" });
  return tempList;
}



