import PlateType from '../../../js/enum.js'
import { customItem } from '../../../js/utility.js'
export function getCommunicationAddInfo(plateType, data) { //data 
  var tempList = [];
  if (plateType === PlateType.Leads) {
    tempList.push(customItem('', 'content', "textarea", "1"));
  } else if (plateType === PlateType.Customer || plateType === PlateType.CustomerPool) {
    tempList.push(customItem('', 'content', "textarea", "1"));
    tempList.push(customItem('', 'status_id', "select", "0", '', '', data));
    tempList.push(customItem('', 'nextstep_time', "datetime", "0", '', '', 'YYYY-MM-DD HH:mm'));
    tempList.push(customItem('', 'id', "business", "0"));
  } else if (plateType === PlateType.Business) {
    tempList.push(customItem('', 'content', "textarea", "1"));
    tempList.push(customItem('', 'type', "select", "0", '', '', data));
    tempList.push(customItem('', 'nextstep_time', "datetime", "0", '', '', 'YYYY-MM-DD HH:mm'));
  }
  return tempList;
}

export function getReceivablesAddInfo(name, data, userInfo) { //data 
  var tempList = [];
  tempList.push(customItem('', 'contract_id', "text", "1", data.contract_id, data.contract_name, '', true));
  tempList.push(customItem('', 'customer_id', "text", "1", data.customer_id, data.customer_name, '', true));
  tempList.push(customItem('', 'name', "text", "1", name, name));
  tempList.push(customItem('', 'price', "text", "1"));
  tempList.push(customItem('', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('', 'description', "textarea", "1"));
  return tempList;
}

export function getReceivingorderAddInfo(data, userInfo) { //data    
  var tempList = [];
  tempList.push(customItem('', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('', 'account_id', "select", "1", '', '', data));
  tempList.push(customItem('/', 'receipt_account', "text", "1", '', '', '', true));
  tempList.push(customItem('', 'company', "text", "1", '', '', '', true));
  tempList.push(customItem('', 'money', "text", "1"));
  tempList.push(customItem('', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('', 'description', "textarea", "1"));
  return tempList;
}



