
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
import { AjaxSilentHttp } from './js/ajaxHttp.js'
import { copyObject } from './js/utility.js'
import event from './js/Event'
import Color from './js/color.js'
import PlateType from './js/enum.js'
import { PLATFORM } from './config.js'

var self = this;
export default {
name: 'app',
components: {
XHeader,
ViewBox,
Tabbar,
TabbarItem,
Previewer,
Loading
},
data() {
return {
height: '100%',
animate: '',
searchText: "",
previewerlist: [],
previewerShow: false, //
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width }


},
shareEl: true,
shareButtons: [
{ id: 'download', label: '', url: '{{raw_image_url}}', download: true }
],
isClickableElement: function(el) {
if (el.className === 'pswp__share--download' && PLATFORM !== 'web') {
if (!self.previewerShow) {
self.previewerShow = true;
cordova.plugins.photoLibrary.requestAuthorization(
function() {
cordova.plugins.photoLibrary.saveImage(el.href, 'MXCRM', function(libraryItem) {
self.previewerShow = false;
alert("");
}, function(err) {
self.previewerShow = false;
alert('');
});

},
function(err) {
self.previewerShow = false;
alert('');

}, // if options not provided, defaults to {read: true}.
{
read: true,
write: true
}
);
}
return true;
}
return el.tagName === 'A';
}
},
}
},
directives: {
focus: {

inserted: function(el) {
el.focus()
}
}
},





mounted() {



if (PLATFORM !== 'web') {
xgpush.on('textmessage', function(e) {

console.log('textmessage-----');
console.log(e);
alert(JSON.parse(e));
});
}

event.$on('previewerAction', function(data) {
var copyData = copyObject(data);
this.previewerlist = copyData.list;
this.$nextTick(() => {
this.$refs.previewer.show(copyData.index);
});
}.bind(this));
this.updatePermission();
},
beforeDestroy() {
event.$off("previewerAction");
},
computed: {
...mapState({
route: state => state.route,
xheaderRightOptions: state => state.xheaderRightOptions,
xheaderShowAdd: state => state.xheaderShowAdd,
userInfo: state => state.userInfo,
direction: state => state.direction
}),
showXheader() {
if (this.route.name === 'welcome') {
return false;
}
return true;
},
xheaderShowFilter() {
if ((this.route.name === 'relatelist' || this.route.name === 'list') && this.route.params.type === PlateType.Product) { //6 
return true;
}
if (this.route.name === 'examinelist' || this.route.name === 'examinemylist') { //
return true;
}
return false;
},
xheaderFilterImage() {
if ((this.route.name === 'relatelist' || this.route.name === 'list') && this.route.params.type === PlateType.Product) { //6 
return require('./assets/filter.png');
}
return require('./assets/examine_filter.png');
},
PlateType() {
return PlateType;
},
Color() {
return Color;
},
leftOptions() {
return {
showBack: this.leftshow
}
},
componentName() {
if (this.route.path) {
const parts = this.route.path.split('/')
if (parts.length === 2) {
return parts[1];
}
return parts[2]
}
},
showTabbar() {
if (this.route.name === 'news' || this.route.name === 'office' || this.route.path === '/' || this.route.name === 'dynamic' || this.route.name === 'my') {
return true;
}
return false;
},
bottomPadding() {
if (this.route.name === 'news' || this.route.name === 'office' || this.route.path === '/' || this.route.name === 'dynamic' || this.route.name === 'my') {
return '55px';
}
return '0px';
},
title() {
this.$vux.loading.hide();

if (this.route.name === 'dynamic') return ''


if (this.route.name === 'news') {
return ''
} else if (this.route.name === 'notice') return ''
else if (this.route.name === 'articledetails') return ''
else if (this.route.name === 'littleassistant') {
if (this.route.params.type === '1') {
return ''
} else if (this.route.params.type === '2') {
return ''
}
}


if (this.route.name === 'login') return ''
else if (this.route.path === '/') return 'CRM'
else if (this.route.name === 'list' || this.route.name === 'relatelist') { //crm
if (this.route.params.type === '7') {
return ''
} else if (this.route.params.type === '3') {
return ''
} else if (this.route.params.type === '18') {
return ''
} else if (this.route.params.type === '5') {
return ''
} else if (this.route.params.type === '4') {
return ''
} else if (this.route.params.type === '8') {
return ''
} else if (this.route.params.type === '6') {
return ''
}
} else if (this.route.name === 'maindetail' || this.route.name === 'crmdetail' || this.route.name === 'mainrelatedetail') { //CRM
return ''
} else if (this.route.name === 'addrelate') { //CRM
return ''
} else if (this.route.name === 'adddata') { //CRM
if (this.route.params.action === 'transform') {
return ''
} else if (this.route.params.action === 'edit') {
return ''
}
return ''
} else if (this.route.name === 'selectlist') { //CRM
return ''
} else if (this.route.name === 'filepreview') { //
return ''
} else if (this.route.name === 'moneyhistory') { //
return ''
} else if (this.route.name === 'relevanceaproduct') { //
return ''
} else if (this.route.name === 'moresort') { //
return ''
} else if (this.route.name === 'financemain') { //
return ''
} else if (this.route.name === 'financeadd') { //
if (this.route.params.action === 'default') {
return ''
} else if (this.route.params.action === 'edit') {
return ''
}
} else if (this.route.name === 'financeopinion') {
return ''
}


if (this.route.name === 'office') {
return ''
} else if (this.route.name === 'addresslist') {
return ''
} else if (this.route.name === 'loglist') {
return ''
} else if (this.route.name === 'logdetail') {
return ''
} else if (this.route.name === 'user') {
return ''
} else if (this.route.name === 'createlog') {
if (this.route.params.type === '4') {
return ''
} else if (this.route.params.type === '3') {
return ''
} else if (this.route.params.type === '2') {
return ''
}
} else if (this.route.name === 'createcomment') {
return ''
} else if (this.route.name === 'schedule') {
return ''
} else if (this.route.name === 'createschedule') {
return ''
} else if (this.route.name === 'conmmondetail') {
return ''
} else if (this.route.name === 'fieldsign' || this.route.name === 'createsign') {
return ''
} else if (this.route.name === 'teamsign') {
return ''
} else if (this.route.name === 'signhistory') {
return ''
} else if (this.route.name === 'mapchoice') {
return ''
} else if (this.route.name === 'rangechoice') {
return ''
}


else if (this.route.name === 'officework') {
return ''
} else if (this.route.name === 'checkstatistics') {
return ''
} else if (this.route.name === 'checkmonth') {
return ''
} else if (this.route.name === 'checkset') {
return ''
}

else if (this.route.name === 'examinemain') {
return ''
} else if (this.route.name === 'examinelist') {
return ''
} else if (this.route.name === 'examinemylist') {
return ''
} else if (this.route.name === 'examinehandle') {
if (this.route.params.type === '1') {
return ''
} else if (this.route.params.type === '2') {
return ''
} else if (this.route.params.type === '3') {
return ''
} else if (this.route.params.type === '4') {
return ''
} else if (this.route.params.type === '5') {
return ''
} else if (this.route.params.type === '6') {
return ''
}
} else if (this.route.name === 'examineview') {
return ''
} else if (this.route.name === 'examineopinion') {
return ''
}

else if (this.route.name === 'taskhandle') {
if (this.route.params.action === 'default') {
return ''
} else if (this.route.params.action === 'edit') {
return ''
}
} else if (this.route.name === 'taskview') {
return ''
}




if (this.route.name === 'my') { return '' } else if (this.route.name === 'set') { return '' } else if (this.route.name === 'about') { return '' }

return this.componentName ? `${this.componentName}` : ''
},
searchshow() {
return /List/.test(this.route.path);
},
showNavHeaderType() {
if (this.$route.name === 'list' || this.$route.name === 'project' || this.$route.name === 'tasklist') return 1; 
if (this.$route.name === 'relatelist' && this.route.params.action === 'select') return 1;
if (this.$route.name === 'crmsearch' || this.$route.name === 'mapchoice' || this.$route.name === 'tasksearch') return 2; 
return 0; 
},
searchPlacehold() {
if (this.showNavHeaderType === 2 || this.showNavHeaderType === 1) {
if (this.$route.name === 'mapchoice') {
return '';
} else if (this.$route.name === 'project') {
return '';
} else if (this.$route.name === 'tasklist') {
return '';
} else if (this.$route.name === 'tasksearch') {
if (this.route.params.type === 'project') {
return '';
} else if (this.route.params.type === 'tasklist') {
return '';
}
} else {
if (this.route.params.type === PlateType.Leads) {
return '';
} else if (this.route.params.type === PlateType.Customer || this.route.params.type === PlateType.CustomerPool) {
return '';
} else if (this.route.params.type === PlateType.Business) {
return '';
} else if (this.route.params.type === PlateType.Contract) {
return '';
} else if (this.route.params.type === PlateType.Contacts) {
return '';
} else if (this.route.params.type === PlateType.Product) {
return '';
} else if (this.route.params.type === PlateType.FinanceReceivables) {
return '';
} else if (this.route.params.type === PlateType.FinanceReceivingorder) {
return '';
}
}
return '';
}
},
leftshow() {
if (this.route.path === '/news' || this.route.path === '/office' || this.route.path === '/' || this.route.path === '/dynamic' || this.route.path === '/my' || this.route.name === 'welcome') {
return false;
}
return true;
},
showSave() {
if (this.route.name === 'adddata' || this.route.name === 'addrelate' || this.route.name === 'createlog' || this.route.name === 'createcomment' || this.route.name === 'createschedule' || this.route.name === 'examinehandle' || this.route.name === 'examineopinion' || this.route.name === 'taskhandle' || this.route.name === 'moresort' || this.route.name === 'financeadd' || this.route.name === 'financeopinion' || this.route.name === 'checkset') {
return true;
} else if (this.route.name === 'selectlist' && (this.route.params.selecttype === '4' || this.route.params.selecttype === '5')) { //
return true;
}
return false;
}
},
methods: {
onClickMore() {
event.$emit('onClickMore');
},
navBack() {
if (this.route.name === 'crmsearch' || this.route.name === 'tasksearch') { // 
this.searchText = '';
}
this.$route ? this.$router.back() : window.history.back();
},
adddata() {
event.$emit('gotoAddData');
},
filterdata() {
event.$emit('filterData'); 
},
saveDone() {
event.$emit('saveDone'); 
},
handleInput(event) {

},
onFocus() {

},
enterCRMSearch() { //CRM
if (this.$route.name === 'project' || this.$route.name === 'tasklist') {
this.$router.push({
name: 'tasksearch',
params: {
type: this.$route.name,
typeid: this.route.params.id ? this.route.params.id : '-1',
action: this.route.params.action
} //
})
} else {
this.$router.push({
name: 'crmsearch',
params: this.$route.params 
})
}

},
onBlur() {

},
submit() {
this.$refs.input.blur();
event.$emit('searchtextDone', this.searchText);
},
onIndexChange(newIndex, oldIndex) {
var name = '';
switch (newIndex) {
case 0:
name = 'dynamic';
break;
case 1:
name = 'news';
break;
case 2:
name = 'crm';
break;
case 3:
name = 'office';
break;
case 4:
name = 'my';
break;
default:
}
this.$router.replace({
name: name
})
},
updatePermission() {
AjaxSilentHttp('POST', 'm=user&a=permission_data', {}, this, function(res, wself, success) {
if (success) {
wself.getPermission(res);
}
});
},
getPermission(res) {
var temps = ['customer', 'business', 'knowledge', 'contacts', 'product', 'leads', 'contract', 'announcement', 'examine', 'event', 'sign', 'task', 'address_book', 'finance'];
var tempsFinance = ["receivables", "receivingorder", "payables", "paymentorder"];
this.userInfo = res.admin;
if (res.admin === 1) {
for (var i = 0; i < temps.length; i++) {
this.userInfo[temps[i]] = true;
}
for (var i = 0; i < tempsFinance.length; i++) {
this.userInfo[tempsFinance[i]] = true;
}
} else if (res.admin === 0) {
for (var i = 0; i < temps.length; i++) {
var item = temps[i];
var exist = false;
for (var j = 0; j < res.permission.length; j++) {
var smallItem = res.permission[j];
if (item === smallItem) {
exist = true;
}
}
this.userInfo[item] = exist;
}

for (var i = 0; i < tempsFinance.length; i++) {
var item = tempsFinance[i];
var exist = false;
for (var j = 0; j < res.finance_type.length; j++) {
var smallItem = res.finance_type[j];
if (item === smallItem) {
exist = true;
}
}
this.userInfo[item] = exist;
}
}
if (this.userInfo) {
this.$store.dispatch('setUserInfo', this.userInfo);
window.sessionStorage.user = JSON.stringify(this.userInfo);
window.localStorage.user = JSON.stringify(this.userInfo);
}

},
}
}




